.social-networks__list {
  .margin-padding();

  display: flex;
  margin: 0 auto;
  align-items: center;
}

.social-networks__item {
  background-color: #81b3d2;
  width: 46px;
  height: 48px;
}

.social-networks__item:not(:first-child) {
  margin-left: 6px;
}

.icon-tw {
  margin: 17px 14px;
}

.icon-fb {
  margin: 14px 19px 16px;
}

.icon-yt {
  margin: 17px 13px;
}

@media (min-width: 768px) {
  .social-networks__list {
    margin: 0 auto;
  }
}
